/*--------------------------------------------------------------
# Utilities
--------------------------------------------------------------*/
@tailwind utilities;
:root {
  /* MAIN COLORS */
  /* Theme Colors added in config-theme.json */
  /* COLORS */
  --color__background-body: var(--color__white);
  --color__background-button: var(--color__black);
  --color__background-hr: #ccc;
  --color__background-pre: var(--color__background-hr);
  --color__background-ins: var(--color__background-hr);
  --color__text-main: var(--color__black);
  --color__text-input: var(--color__text-main);
  --color__text-input-focus: var(--color__text-main);
  --color__link: inherit;
  --color__link-visited: inherit;
  --color__link-hover: inherit;
  /* TYPOGRAPHY */
  --font__main: "Roboto Mono", "monospace";
  --font__serif: "Raisonne Pro", "Lucida Bright", "Lucidabright", "Lucida Serif", "Lucida", "DejaVu Serif", "Bitstream Vera Serif", "Liberation Serif", "Georgia", "serif";
  --font__line-height-body: 1.5;
  --font__line-height-pre: 1.6;
  --font__letter-spacing: normal;
  --font__letter-spacing--xl: 0.15rem;
  --font__size-root-small: 3.7vw;
  --font__size-root-medium: 1.4vw;
  --font__size-root-large: 1.25vw;
  /* STRUCTURE */
  --size__site-main: 100%;
  --size__site-small: 95vw;
  --size__site-medium: 72vw;
  --size__site-large: 70vw;
  --size__site-medium-sm: 68vw;
  --size__site-large-sm: 64vw;
  --size__site-medium-lg: 85vw;
  --size__site-large-lg: 85vw;
  --size__site-sidebar: 25vw;
  /* Bezier Curves */
  --bezier__default: cubic-bezier(0.55, 0, 0.1, 1);
  /* RWD */
  --break__small: 754px;
  --break__medium: 992px;
  --break__large: 1200px;
}

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

[hidden],
template {
  display: none;
}

a {
  background-color: transparent;
}

a:active,
a:hover {
  outline: 0;
}

abbr[title] {
  border-bottom: 1px dotted;
}

b,
strong {
  font-weight: bold;
}

dfn {
  font-style: italic;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

mark {
  background: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sup {
  top: -0.5em;
}

sub {
  bottom: -0.25em;
}

img {
  border: 0;
}

svg:not(:root) {
  overflow: hidden;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
}

pre {
  overflow: auto;
}

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer;
}

button[disabled],
html input[disabled] {
  cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

input {
  line-height: normal;
}

input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  height: auto;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

legend {
  border: 0;
  padding: 0;
}

textarea {
  overflow: auto;
}

optgroup {
  font-weight: bold;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

td,
th {
  padding: 0;
}

.clear:before,
.clear:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
  content: "";
  display: table;
  table-layout: fixed;
}

.clear:after,
.site-header:after,
.site-content:after,
.site-footer:after {
  clear: both;
}

.clearfix::before,
.clearfix::after {
  content: " ";
  display: table;
}

.clearfix::after {
  clear: both;
}

.stroke-current {
  stroke: currentColor;
}

.list-reset {
  list-style: none;
  padding: 0;
}

.u-breakout {
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  padding-left: calc(50vw - 50%);
  padding-right: calc(50vw - 50%);
}

.object-fit-cover,
.object-cover {
  object-fit: cover;
  font-family: "object-fit: cover;";
}

.object-fit-contain,
.object-contain {
  object-fit: contain;
  font-family: "object-fit: contain;";
}

.inset {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (min-width: 992px) {
  .md\:inset {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

@media (min-width: 1200px) {
  .lg\:inset {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
}

@media (min-width: 992px) {
  .md\:sticky {
    position: -webkit-sticky;
    position: sticky;
  }
}

@media (min-width: 1200px) {
  .lg\:sticky {
    position: -webkit-sticky;
    position: sticky;
  }
}

/*--------------------------------------------------------------
# Base
--------------------------------------------------------------*/
/*---------------------------
## Elements
---------------------------*/
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit;
}

body {
  background: var(--color__background-body);
}

blockquote, q {
  quotes: "" "";
}
blockquote:before, blockquote:after, q:before, q:after {
  content: "";
}

hr {
  background-color: var(--color__background-hr);
  border: 0;
  height: 1px;
  margin-bottom: 1.5em;
}

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */
}

figure {
  margin: 0;
  /* Extra wide images within figure tags don't overflow the content area. */
}

/* LINKS */
a {
  color: var(--color__link);
}
a:hover, a:focus, a:active {
  color: var(--color__link-hover);
}
a:focus {
  outline: thin dotted;
  outline-offset: 0.25rem;
}
a:hover, a:active {
  outline: 0;
}

/* LISTS */
ul, ol {
  margin: 0 0 0 0;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 0;
}

dt {
  font-weight: bold;
}

dd {
  margin: 0 0 0 0;
}

/* TABLES */
table {
  margin: 0 0 0;
  width: 100%;
}

/*---------------------------
## Typography
---------------------------*/
html {
  font-size: var(--font__size-root-small);
  letter-spacing: var(--font__letter-spacing);
}
@media (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: portrait) {
  html {
    font-size: 3vw;
  }
}
@media (min-width: 992px) {
  html {
    font-size: var(--font__size-root-medium);
  }
}
@media (min-width: 1200px) {
  html {
    font-size: var(--font__size-root-large);
  }
}

body,
button,
input,
select,
optgroup,
textarea {
  color: var(--color__text-main);
  font-family: var(--font__main);
  font-weight: 300;
  font-size: 1rem;
  line-height: var(--font__line-height-body);
}

/* HEADINGS */
.entry-title {
  font-size: 4.5rem;
  line-height: 1;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  clear: both;
  font-weight: bold;
  line-height: 1.1;
  margin: 0;
  letter-spacing: 0.05rem;
  font-family: var(--font__serif);
}

h1, .h1 {
  font-size: 2.1rem;
  line-height: 1.25;
}
@media (min-width: 992px) {
  h1, .h1 {
    font-size: 2.1rem;
  }
}

h2, .h2 {
  font-size: 2rem;
}
@media (min-width: 992px) {
  h2, .h2 {
    font-size: 2rem;
  }
}

h3, .h3 {
  font-size: 1.7rem;
}
@media (min-width: 992px) {
  h3, .h3 {
    font-size: 1.8rem;
  }
}

h4, .h4 {
  line-height: 1.5;
  font-size: 1.5rem;
}
@media (min-width: 992px) {
  h4, .h4 {
    font-size: 1.5rem;
  }
}

h5, .h5 {
  font-size: 1.25rem;
}

h6, .h6 {
  font-size: 1rem;
  font-weight: bold;
}

legend {
  line-height: 1.4;
}

/* COPY */
p {
  margin-top: 0;
  margin-bottom: 1em;
}
p:last-child {
  margin-bottom: 0;
}
p:empty {
  display: none;
}
p.first-letter::first-letter {
  color: white;
  float: left;
  font-size: 1.9rem;
  line-height: 0.9;
  margin-right: 0.2rem;
  font-family: var(--font__serif);
  margin-top: -0.29rem;
}

a.link {
  position: relative;
  letter-spacing: var(--font__letter-spacing--xl);
  text-decoration: none;
  font-size: 0.6rem;
  text-transform: uppercase;
}
a.link:after {
  content: "";
  position: absolute;
  bottom: -0.2rem;
  left: 0;
  opacity: 1;
  width: 100%;
  height: 1px;
  transform-origin: 50% 50%;
  transform: scaleX(1);
  background-color: currentColor;
  transition: all 0.4s cubic-bezier(0.55, 0, 0.1, 1);
}
a.link:hover:after {
  transform: scaleX(0.8);
}
a.link:before {
  /*
  content: '';
  position: absolute;
  left: -0.5rem;
  top: -1rem;
  height: calc(100% + 2rem);
  width: calc(100% + 1rem);
  */
}

dfn, cite, em, i {
  font-style: italic;
}

blockquote {
  margin: 0 1.5em;
}

address {
  margin: 0 0 1.5em;
}

abbr, acronym {
  border-bottom: 1px dotted var(--color__border-abbr);
  cursor: help;
}

mark, ins {
  background: var(--color__background-ins);
  text-decoration: none;
}

big {
  font-size: 125%;
}

/*---------------------------
## Forms
---------------------------*/
/* BUTTONS */
button,
input[type=button],
input[type=reset],
input[type=submit] {
  border: 1px solid;
  border-color: var(--color__border-button);
  border-radius: 3px;
  background: var(--color__background-button);
  color: rgba(0, 0, 0, 0.8);
  font-size: 0.75rem;
  line-height: 1;
  padding: 0.6em 1em 0.4em;
}
button:active, button:focus, button:hover,
input[type=button]:active,
input[type=button]:focus,
input[type=button]:hover,
input[type=reset]:active,
input[type=reset]:focus,
input[type=reset]:hover,
input[type=submit]:active,
input[type=submit]:focus,
input[type=submit]:hover {
  border-color: var(--color__border-button-focus);
}

/* FIELDS */
input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search],
select,
textarea {
  color: var(--color__text-input);
  border: 1px solid var(--color__border-input);
  border-radius: 3px;
}
input[type=text]:focus,
input[type=email]:focus,
input[type=url]:focus,
input[type=password]:focus,
input[type=search]:focus,
select:focus,
textarea:focus {
  color: var(--color__text-input-focus);
}

input[type=text],
input[type=email],
input[type=url],
input[type=password],
input[type=search] {
  padding: 3px;
}

textarea {
  padding-left: 3px;
  width: 100%;
}

/*--------------------------------------------------------------
# Layout
--------------------------------------------------------------*/
/*---------------------------
## Grid
---------------------------*/
.container {
  width: var(--size__site-main);
  max-width: 100%;
  display: block;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
@media (min-width: 754px) {
  .container {
    width: var(--size__site-small);
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 992px) {
  .container {
    width: var(--size__site-medium);
    padding-left: 0;
    padding-right: 0;
  }
}
@media (min-width: 1200px) {
  .container {
    width: var(--size__site-large);
  }
}
@media (min-width: 992px) {
  .container-sm {
    width: var(--size__site-medium-sm);
  }
}
@media (min-width: 1200px) {
  .container-sm {
    width: var(--size__site-large-sm);
  }
}
@media (min-width: 992px) {
  .container-lg {
    width: var(--size__site-medium-lg);
  }
}
@media (min-width: 1200px) {
  .container-lg {
    width: var(--size__site-large-lg);
  }
}
.container.flex {
  display: flex;
}
@media (min-width: 992px) {
  .container.md\:flex {
    display: flex;
  }
}

/*---------------------------
## Background
---------------------------*/
/*---------------------------
## Header
---------------------------*/
.site__header {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
  background-color: transparent;
  z-index: 99999;
  transform: translateY(0rem);
  transition: all 0.45s cubic-bezier(0.55, 0, 0.1, 1);
}
@media (min-width: 992px) {
  .site__header {
    padding-top: 0;
    padding-bottom: 0;
  }
}
.site__navigation {
  transition: transform 0.6s ease-in-out;
}

.site__brand {
  width: 5.5rem;
  position: relative;
  z-index: 10000;
  line-height: 0;
  transition: all 0.4s ease-in-out;
  margin-top: 0rem;
}
@media (min-width: 992px) {
  .site__brand {
    position: absolute;
    left: 50%;
    margin-left: -3.25rem;
    width: 6.5rem;
    top: 1rem;
    padding-top: 0;
  }
}
.site__brand .o-logo path {
  transition: all 0.4s ease-in-out;
}

.site__brand-sign {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.4s ease-in-out, visibility 0.4s ease-in-out;
  will-change: opacity, visibility;
}

.site__navigation-social svg {
  fill: var(--color__white);
}
.site__navigation-social svg path[fill="#FFFFFF"] {
  fill: var(--color__white);
}

@media (max-width: 992px) {
  body.is-navigation--open .site__brand-sign .o-logo path[fill="#FFFFFF"] {
    fill: white;
  }
  body.is-navigation--open .site__brand-sign .o-logo path[stroke="#FFFFFF"] {
    stroke: white;
  }
  body.is-navigation--open .site__navigation-social svg,
body.is-navigation--open .site__navigation-toggle svg {
    fill: white;
  }
  body.is-navigation--open .site__navigation-social svg path[fill="#FFFFFF"],
body.is-navigation--open .site__navigation-toggle svg path[fill="#FFFFFF"] {
    fill: white;
  }
  body.is-navigation--open .site__navigation-social svg circle[stroke="#FFFFFF"],
body.is-navigation--open .site__navigation-toggle svg circle[stroke="#FFFFFF"] {
    stroke: white;
  }
}

/*---------------------------
## Navigation
---------------------------*/
.site__navigation {
  --nav__icon-size--sm: 2rem;
  --nav__icon-size--lg: 2.25rem;
  --nav__toggle-size--sm: 2.75rem;
  --nav__contact-size--md: 13rem;
  --nav__contact-size--lg: 15rem;
  padding-top: 0rem;
  position: relative;
  z-index: 9999;
}
@media (min-width: 992px) {
  .site__navigation {
    padding-top: 0rem;
    padding-bottom: 0;
    z-index: 1;
    width: 100%;
  }
}

.site__navigation-toggle {
  display: block;
  background-color: transparent !important;
  border: none;
  border-radius: 0 0 0 0;
  font-size: 1rem;
  color: transparent;
  padding: 0rem;
  overflow: hidden;
  position: relative;
  z-index: 999999;
  display: flex !important;
  flex-wrap: wrap;
  align-items: center;
  margin-left: 0.25rem;
  width: var(--nav__toggle-size--sm);
  height: var(--nav__toggle-size--sm);
}
.site__navigation-toggle[aria-expanded=true] svg path[fill="#FFFFFF"] {
  fill: var(--color__white-full) !important;
}
.site__navigation-toggle[aria-expanded=true] svg circle[stroke="#FFFFFF"] {
  stroke: var(--color__white-full) !important;
}
@media (min-width: 992px) {
  .site__navigation-toggle {
    display: none !important;
  }
}
.site__navigation-toggle > svg {
  width: inherit;
  height: inherit;
}

.site__navigation-social li {
  line-height: 0;
}
.site__navigation-social svg {
  width: var(--nav__icon-size--sm);
  height: var(--nav__icon-size--sm);
}
@media (min-width: 992px) {
  .site__navigation-social svg {
    width: var(--nav__icon-size--lg);
    height: var(--nav__icon-size--lg);
  }
}

.site__navigation ul.l-navigation-main {
  list-style: none;
  margin: 0;
  padding: 0;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;
  font-family: var(--font__serif);
  font-size: 1.25rem;
}
.site__navigation ul.l-navigation-main::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: var(--color__blue);
  opacity: 1;
  z-index: -1;
}
@media (min-width: 992px) {
  .site__navigation ul.l-navigation-main::before {
    display: none;
  }
}
.site__navigation ul.l-navigation-main li {
  display: block;
  width: 100%;
  text-align: center;
}
.site__navigation ul.l-navigation-main li:first-child {
  margin-top: -2rem;
}
.site__navigation ul.l-navigation-main li a {
  text-decoration: none;
  color: var(--color__white-full);
  text-transform: uppercase;
  display: inline-block;
  padding: 0.5rem 2rem;
}
.site__navigation ul.l-navigation-main li + li {
  margin-top: 0.5rem;
}
@media (min-width: 992px) {
  .site__navigation ul.l-navigation-main {
    position: relative;
    height: auto;
    padding: 1.5rem 0 1.5rem 2rem;
    border-bottom: 1px solid var(--color__yellow);
    display: flex;
    align-items: flex-start;
    margin-right: 0rem;
    margin-left: 0;
    width: calc(100% - var(--nav__contact-size--md));
    font-size: 0.7rem;
    backdrop-filter: none;
  }
  .site__navigation ul.l-navigation-main li {
    display: inline-block;
    width: auto;
    text-align: left;
  }
  .site__navigation ul.l-navigation-main li:first-child {
    margin-top: 0;
  }
  .site__navigation ul.l-navigation-main li + li {
    margin-left: 1rem;
    margin-top: 0;
  }
  .site__navigation ul.l-navigation-main li a {
    padding: 0;
    display: block;
    color: black;
    position: relative;
  }
  .site__navigation ul.l-navigation-main li a::after {
    content: "";
    transition: all 0.2s ease-in-out;
    position: absolute;
    left: 0;
    right: 0;
    bottom: -2px;
    height: 3px;
    background-color: currentColor;
    transform: scaleX(0);
  }
  .site__navigation ul.l-navigation-main li.current-menu-item > a {
    position: relative;
  }
  .site__navigation ul.l-navigation-main li.current-menu-item > a::after {
    transform: scaleX(1);
  }
}
@media (min-width: 1200px) {
  .site__navigation ul.l-navigation-main {
    padding: 1.5rem 0 1.5rem 4rem;
    width: calc(100% - var(--nav__contact-size--lg));
  }
}
.site__navigation .site__navigation-social {
  padding: 0.5rem 0 0 0;
  z-index: 1;
}

.site__navigation-contact {
  width: var(--nav__contact-size--lg);
}

/**
 * Animations
 */
.site__navigation ul.l-navigation-main {
  opacity: 0;
  visibility: hidden;
  backdrop-filter: blur(0px);
  transition: opacity 0.05s 0.5s, visibility 0.05s 0.5s;
