/*--------------------------------------------------------------
# Objects
--------------------------------------------------------------*/
.o-mouse {
  display: block;
  position: absolute;
  margin-left: -1rem;
  bottom: 2rem;
  right: 2rem;
  z-index: 999;
  width: 1.5rem;
  height: 2.2rem;
  border: 0.1rem solid #fff;
  border-radius: 1rem;
  opacity: 1;
  visibility: visible;
  transition: all 0.5s ease-in-out;
  mix-blend-mode: difference;
}
@media (min-width: 992px) {
  .o-mouse {
    bottom: 4rem;
    right: 4rem;
  }
}
.o-mouse--center {
  right: calc(50% - 0.75rem);
  margin-left: 0;
}
.o-mouse--left {
  right: calc(100% - 2rem);
}
@media (min-width: 992px) {
  .o-mouse--left {
    right: calc(100% - 4rem);
  }
}
.o-mouse--left-content {
  right: auto;
  left: 3rem;
}
@media (min-width: 992px) {
  .o-mouse--left-content {
    left: calc(13.5vw + 1rem);
  }
}
@media (min-width: 1200px) {
  .o-mouse--left-content {
    left: calc(15vw + 1rem);
  }
}

.o-mouse__wheel {
  display: block;
  margin: 0 0 0 -0.125rem;
  position: absolute;
  top: 0.5rem;
  left: 50%;
  width: 0.25rem;
  height: 0.25rem;
  background: #fff;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  border-radius: 50%;
  -moz-animation: scroll-wheel 1.8s linear 0s infinite normal none;
  -webkit-animation: scroll-wheel 1.8s linear 0s infinite normal none;
  animation: scroll-wheel 1.8s linear 0s infinite normal none;
}

@keyframes scroll-wheel {
  0% {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
  35% {
    transform: translate3d(0, 7px, 0);
    opacity: 1;
  }
  50% {
    transform: translate3d(0, 10px, 0);
    opacity: 0;
  }
  50.001% {
    transform: translate3d(0, 0, 0);
    opacity: 0;
  }
  80% {
    transform: translate3d(0, 0, 0);
    opacity: 0;
  }
}
