.o-menu {
  cursor: pointer;
}

.o-menu__line {
  transform-origin: 41px 41px;
  transition: opacity 600ms cubic-bezier(0.4, 0, 0.2, 1), transform 600ms cubic-bezier(0.4, 0, 0.2, 1);
}

.o-menu__line--1,
.o-menu__line--3 {
  transform: rotate(0deg) translate(0px, 0px) scaleX(1);
  will-change: transform;
}

.o-menu__line--2 {
  opacity: 1;
  transform: translateX(0);
  will-change: opacity, transform;
}

.o-menu__outline {
  stroke-dasharray: 92.527275085;
  transform: rotate(-90deg);
  transform-origin: 41px 41px;
  transition: transform 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1);
  will-change: transform, stroke-dasharray;
}

.is-open .o-menu__line--1 {
  transform: rotate(45deg) translate(0px, -7px) scaleX(0.8);
}

.is-open .o-menu__line--2 {
  opacity: 0;
  transform: translateX(10px);
}

.is-open .o-menu__line--3 {
  transform: rotate(-45deg) translate(0px, 7px) scaleX(0.8);
}

.is-open .o-menu__outline {
  stroke-dasharray: 185.0545501709;
  transform: rotate(90deg);
}

.o-shape--door {
  border-radius: 20rem 20rem 0 0;
}

